<template>
    <div class="login">
      <el-form ref="formLogin" :model="form" class="login-from" :rules="rules">
        <el-form-item>
            <div class="login-img">
                <img src="../assets/avatar.jpg" alt="登录图片" style="width: 120px; height: 120px; margin-left: -10px; margin-top: -10px;">
            </div>
        </el-form-item>
        <el-form-item prop="username">
           <el-input v-model="form.username" prefix-icon="myicon myicon-user"></el-input>
        </el-form-item>
         <el-form-item prop="password">
           <!-- @keydown.native.enter按下确定键的时候触发 -->
           <el-input v-model="form.password" type="password" prefix-icon="myicon myicon-key" @keydown.native.enter="handleLogin('formLogin')"></el-input>
        </el-form-item>
         <el-form-item>
          <el-button class="login-btn" @click="handleLogin('formLogin')">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
import {Login} from '@/api/index'
export default {
  name: 'Login',
  data () {
    return {
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    handleLogin (formLogin) {
      // 检验用户是否输入
      this.$refs.formLogin.validate((isValide) => {
        // 如果检验通过则发起请求
        if (isValide) {
          Login(this.form)
            .then(res => {
              if (res.code === '1') {
                this.$message.success('登录成功')
                // 设置过期时间
                let ctimer = new Date(new Date(new Date().toLocaleDateString()).getTime()+24*60*60*1000-1).getTime()
                localStorage.setItem('outTime', ctimer)
                // 设置用户名称
                localStorage.setItem('username', res.data.username)
                localStorage.setItem('mytoken', res.data.token)
                localStorage.setItem('isAdmin', res.data.is_admin)
                // 登录成功跳转到啊首页
                this.$router.push({name: 'home'})
              } else {
                this.$message.error(res.message)
              }
            })
        } else {
          alert('请输入用户名或者密码')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.login{
    width: 100%;
    height: 100%;
    background-color: #2f4050;
    position: fixed;
    .login-from{
        width: 400px;
        margin: 100px auto;
        padding: 0px 40px 15px 40px;
        background-color: #fff;
        position: absolute;
        right: 0;
        left:0;
        .login-img{
             position: relative;
             left: 50%;
             width: 120px;
             height: 120px;
             margin-left: -60px;
             margin-top: -60px;
             box-sizing: border-box;
             border-radius: 50%;
             border: 10px solid #fff;
             box-shadow: 0 1px 5px #ccc;
             overflow: hidden;
        }
        .login-btn{
            width: 100%;
            background-color: blue;
            color: #fff;
            font-size: 20px;
        }
    }
}
@media screen and (max-width: 640px){
  .login .login-from{
    width: 100%;
    padding: 0 5px;
  }
}
</style>



