import axios from 'axios'
// 通过axios.defaults.baseURL设置全局的基准路径
axios.defaults.baseURL = 'http://192.168.1.199:8888/admin/Api'
// 设置请求拦截，目的是拦截所有请求，如果本地有token，设置请求头，可以通过访问，如果没有，不能继续访问了
axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  // 拦截到请求后，首先判断是否有token，如果有的话，就将它取出来，放进config.headers里面
  let mytoken = localStorage.getItem('mytoken')
  if (mytoken) {
  // 设置请求头Authorization 的值为mytoken里面的值
    config.headers['Authorization'] = mytoken
  } else {
    return config
  }
  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})
// 登录验证
export const Login = (params) => { return axios.post('/login', params).then(res => res.data) }
// 登出
export const logout = (params) => { return axios.get('/logout', params).then(res => res.data) }
// 获取分组数据
export const getGroup = (params) => {
  return axios.get('/showGroup', params).then(res => res.data)
}
// 删除分组
export const delGroup = (params) => {
  return axios.post('/delGroup', params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}}).then(res => res.data)
}
// 添加分组
export const addGroup = (params) => {
  return axios.post('/showGroup', params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}}).then(res => res.data)
}
// 获取文章链接
export const getUrl = (params) => {
  return axios.get(`/modifyUrl/${params}`).then(res => res.data)
}
// 添加文章链接
export const addUrl = (params) => {
  return axios.post('/addUrl', params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}}).then(res => res.data)
}
// 删除文章链接
export const delUrl = (params) => {
  return axios.post('/delUrl', params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}}).then(res => res.data)
}
// 修改文章链接
export const editUrl = (params) => {
  return axios.post('/modifyUrl', params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}}).then(res => res.data)
}
// 获取用户列表
export const getUser = (params) => {
  return axios.get('/showUsers', params).then(res => res.data)
}
// 编辑用户列表
export const editUser = (params) => {
  return axios.post('/showUsers', params, {headers: {'Content-Type': 'application/json;charset=UTF-8'}}).then(res => res.data)
}



