<template>
  <div class="users">
     <el-table
      :data="userData"
      border
      style="width: 100%">
      <el-table-column
        prop="username"
        label="用户名"
      >
      </el-table-column>
      <el-table-column
        label="权限"
        >
        <template slot-scope="scope">
          <span>{{scope.row.is_admin === '1'? '管理员':'普通用户'}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="create_time"
        label="创建时间">
      </el-table-column>

         <el-table-column
        label="用户状态">
        <template slot-scope="scope">
          <span>{{scope.row.is_delete === '1'? '已删除':'正常'}}</span>
        </template>
      </el-table-column>
        <el-table-column
        label="操作">
        <template slot-scope="scope">
            <el-button type="danger" v-if="scope.row.is_delete === '1'? false : true" @click="handleDeleteUser(scope.row.user_id)">删除用户</el-button>
        </template>
      </el-table-column>
  </el-table>
  </div>
</template>

<script>
import {getUser, editUser} from '@/api/index'
export default {
  name: 'Users',
  data () {
    return {
      userData: []
    }
  },
  methods: {
    handleDeleteUser (id) {
      let params = {user_id: id}
      this.$confirm('是否删除该用户', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        editUser(JSON.stringify(params))
          .then(res => {
            if (res.code === '1') {
              this.$message.success('删除用户成功')
              getUser()
                .then(res => {
                  if (res.code === '1') {
                    for (let i=0; i<res.users.length; i++) {
                      res.users[i].create_time = res.users[i].create_time.substring(0, 10)
                    }
                    this.userData = res.users
                  }
                })
            }
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  },
  mounted () {
    getUser()
      .then(res => {
        if (res.code === '1') {
          for (let i=0; i<res.users.length; i++) {
            res.users[i].create_time = res.users[i].create_time.substring(0, 10)
          }
          this.userData = res.users
        }
      })
  }
}
</script>

<style lang="scss" scoped>
</style>



