import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login'
import Home from '@/views/home'
import Wecome from '@/components/wecome'
import Group from '@/components/group'
import Article from '@/components/article'
import Users from '@/components/users'
Vue.use(Router)

export default new Router({
  routes: [
    {
      name: 'login',
      path: '/login',
      component: Login
    },
    {
      name: 'home',
      path: '/home',
      component: Home,
      redirect: {name: 'wecome'}, // 重定向到wecome路由
      children: [{
        name: 'wecome',
        path: 'wecome',
        component: Wecome
      },
      {
        name: 'group',
        path: 'group',
        component: Group
      },
      {
        name: 'article',
        path: 'article',
        component: Article
      },
      {
        name: 'users',
        path: 'users',
        component: Users
      }
      ]
    },
    {
      path: '*',
      redirect: {name: 'wecome'}
    }
  ]
})



