<template>
  <div class="article">
    <el-button type="primary"  @click="handleAddurl">添加文章链接</el-button>
      <el-select v-model="value" placeholder="请选择分组" style="width:110px;">
        <el-option
          v-for="item in groupData"
          :key="item.value"
          :label="item.name"
          :value="item.group_id">
        </el-option>
      </el-select>
    <el-button type="primary"  @click="handleSearch">搜索</el-button>
     <el-table
        :data="articleData"
        border
        style="width: 100%;margin-top:10px;">
        <el-table-column
          label="链接名"
          prop="article_name"
        >
        </el-table-column>
         <el-table-column
          label="链接"
          prop="url"
        >
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
              <el-button type="danger" @click="handleDelete(scope.row.article_id)">删除文章链接</el-button>
              <el-button type="warning" @click="handleEdit(scope.row)">修改文章链接</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 弹框修改文章链接 -->
      <el-dialog title="修改文章链接" :visible.sync="dialogEditUrl">
          <el-form>
            <el-form-item label="文章链接">
              <el-input v-model="editUrl" @keydown.native.enter="handleEditurl"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogEditUrl = false">取 消</el-button>
            <el-button type="primary" @click="handleEditurl">确 定</el-button>
          </div>
      </el-dialog>
      <!-- 添加文章链接 -->
      <el-dialog title="添加文章链接" :visible.sync="dialogAddUrl">
          <el-form>
            <el-form-item label="请输入文章链接">
              <el-input v-model="AddUrl"  :rules="{required: true, message: '链接不能为空', trigger: 'blur'}" placeholder="请输入微信文章链接"></el-input>
            </el-form-item>
            <el-form-item label="请选择分组">
             <el-select v-model="value2" placeholder="请选择分组" style="width:110px;">
              <el-option
                v-for="item in groupData"
                :key="item.value"
                :label="item.name"
                :value="item.group_id">
              </el-option>
            </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogAddUrl = false">取 消</el-button>
            <el-button type="primary" @click="handledialogAddurl">确 定</el-button>
          </div>
      </el-dialog>
  </div>
</template>

<script>
import {getUrl, getGroup, addUrl, delUrl, editUrl} from '@/api/index'
export default {
  name: 'Article',
  data () {
    return {
      articleData: [],
      groupData: [],
      value: '',
      value2: '',
      editUrl: '',
      articleId: '',
      dialogEditUrl: false,
      dialogAddUrl: false,
      AddUrl: ''
    }
  },
  methods: {
    // 删除链接
    handleDelete (id) {
      let params = {article_id: id}
      this.$confirm('是否删除该用户', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delUrl(JSON.stringify(params))
          .then(res => {
            if (res.code === '1') {
              this.$message.success('删除链接成功')
              getUrl(this.value.toString())
                .then(res => {
                  if (res.code === 1) {
                    if (typeof (res.urls) !== 'string') {
                      this.value = res.urls[0].group_id
                      this.articleData = res.urls
                    } else {
                      this.articleData = []
                    }
                  } else {
                    this.articleData = res.urls
                    this.$message.error('查询失败，暂无数据')
                  }
                })
            }
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 添加链接
    handleAddurl () {
      this.dialogAddUrl = true
    },
    // 修改链接
    handleEdit (row) {
      this.dialogEditUrl = true
      this.articleId = row.article_id
      this.editUrl = row.url
    },
    handleEditurl () {
      let params ={url: this.editUrl, article_id: this.articleId}
      editUrl(JSON.stringify(params))
        .then(res => {
          if (res.code === '1') {
            this.$message.success('修改链接成功')
            this.dialogEditUrl = false
            getUrl(this.value.toString())
              .then(res => {
                if (res.code === 1) {
                  if (typeof (res.urls) !== 'string') {
                    this.value = res.urls[0].group_id
                    this.articleData = res.urls
                  } else {
                    this.articleData = []
                  }
                } else {
                  this.articleData = res.urls
                  this.$message.error('查询失败，暂无数据')
                }
              })
          }
        })
    },
    // 搜索分组链接
    handleSearch () {
      getUrl(this.value.toString())
        .then(res => {
          if (res.code === 1) {
            if (typeof (res.urls) !== 'string') {
              this.value = res.urls[0].group_id
              this.articleData = res.urls
            } else {
              this.articleData = []
            }
          } else {
            this.articleData = res.urls
            this.$message.error('查询失败，暂无数据')
          }
        })
    },
    // 添加链接
    handledialogAddurl () {
      let reg = /^(http|https):\/\/(mp.weixin.qq.com\/s\/)[\S]?/
      if (!reg.test(this.AddUrl)) {
        this.$message.error('请输入正确的链接')
        return false
      }
      if (!this.value2) {
        this.$message.error('请选择分组')
        return false
      }
      let params = {group_id: this.value2, url: this.AddUrl}
      addUrl(JSON.stringify(params))
        .then(res => {
          if (res.code === '1') {
            this.$message.success('添加文章链接成功')
            this.dialogAddUrl = false
            getUrl(this.value.toString())
              .then(res => {
                if (res.code === 1) {
                  if (typeof (res.urls) !== 'string') {
                    this.value = res.urls[0].group_id
                    this.articleData = res.urls
                  } else {
                    this.articleData = []
                  }
                } else {
                  this.articleData = res.urls
                  this.$message.error('查询失败，暂无数据')
                }
              })
          } else {
            this.$message.erorr('请输入正确的文章链接')
          }
        })
    }
  },
  mounted () {
    // 获取文章链接
    getUrl('1')
      .then(res => {
        if (res.code === 1) {
          if (typeof (res.urls) !== 'string') {
            this.value = res.urls[0].group_id
            this.articleData = res.urls
          } else {
            this.value = 1
          }
        } else {
          this.value = res.group_id
          this.articleData = res.urls
          this.$message.error('查询失败，暂无数据')
        }
      })
    // 获取分组
    getGroup()
      .then(res => {
        if (res.code === 1) {
          this.groupData = res.groups
        }
      })
  }
}
</script>

<style lang="scss" scoped>
@media screen and (max-width: 640px){
  .el-main{
    padding: 0px !important;
  }
}
@media screen and (max-width: 640px){
  .el-button{
   padding: 12px 0;
   margin:0;
  }
}
</style>

<style>
@media screen and (max-width: 640px){
  .el-table .cell{
    padding: 0;
  }
}
@media screen and (max-width: 640px){
  .el-dialog{
    width: 100%;
  }
}
</style>



