 <template>
 <div class="home">
    <el-container>
      <!-- 侧边栏部分 -->
      <el-aside width="auto">
        <div style="height:60px;"></div>
        <!-- default-active表示默认打开哪个index的子菜单 -->
        <el-menu
          class="el-menu-admin"
          :default-active="openNav"
          @open="handleOpen"
          @close="handleClose"
          :collapse="isCollapse"
          background-color="#f9f9f9"
          :router="true"
          >

          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-location" v-if="screenWidth <640? false : true"></i>
               <i v-if="screenWidth <640? true : false" style="margin-left:-15px;">文章列表</i>
              <span>文章列表</span>
            </template>

            <el-menu-item index="article">
              <i class="el-icon-menu"></i>
              <span>文章链接管理</span>
            </el-menu-item>
            <el-menu-item index="group">
              <i class="el-icon-menu"></i>
              <span>分组管理</span>
            </el-menu-item>
          </el-submenu>
          <el-submenu index="2" v-if="isAdmin === '1'? true : false">
            <template slot="title">
              <i class="el-icon-location" v-if="screenWidth <640? false : true"></i>
              <i v-if="screenWidth <640? true : false" style="margin-left:-15px;">用户列表</i>
              <span>用户列表</span>
            </template>
             <el-menu-item index="users">
              <i class="el-icon-menu"></i>
              <span>用户管理</span>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-container>
        <!-- 这是头部 -->
        <el-header>
          <i class="myicon myicon-menu toggle-btn" @click="toggleMenu" v-if="screenWidth <640? false : true"></i>
          <div class="system-title">
            文案分发管理系统
          </div>
          <div class="welcome">
            <span v-if="screenWidth <640? false : true">
              您好，{{username}}
            </span>
            <el-button type="warning" round @click="logout">退出</el-button>
          </div>
        </el-header>

        <!-- 中间主体内容部分，是一个坑，将来通过路由挂载组件用的 -->
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import {getActiveNav} from '@/js/getNav.js'
export default {
  name: 'Home',
  data () {
    return {
      isCollapse: false,
      openNav: '',
      username: '',
      isAdmin: '',
      screenWidth: document.body.clientWidth
    }
  },
  methods: {
    handleOpen () {
    },
    handleClose () {
    },
    toggleMenu () {
      this.isCollapse = !this.isCollapse
    },
    logout () {
      // 退出时将清除掉所有的本地存储，并且跳转回到login页面
      localStorage.setItem('mytoken', '')
      localStorage.setItem('username', '')
      localStorage.setItem('outTime', '')
      localStorage.setItem('isAdmin', '')
      this.$router.push({name: 'login'})
    }
  },
  mounted () {
    window.onload = () => {
      window.screenWidth = document.body.clientWidth
      this.screenWidth = window.screenWidth
    }
    if (this.screenWidth < 640) {
      this.isCollapse = true
    }
    let CurrentTime = new Date().getTime()
    if (CurrentTime > localStorage.getItem('outTime')) {
      this.$message.error('请重新登录')
      this.logout()
    }
    this.username = localStorage.getItem('username')
    this.isAdmin = localStorage.getItem('isAdmin')
    this.openNav = getActiveNav(window.location.href)
  }
}
</script>

<style lang="scss" scoped>
.home {
  height: 100%;
  @media screen and (max-width:640px) {
    .el-main{
      padding:10px;
    }
  }
  // 这里表示不是折叠的时候的menu的样式
  .el-menu-admin:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .el-container {
    height: 100%;
  }
  .el-aside {
    background-color: #F9F9F9;
  }
  .el-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #409EFF;
  }
  .toggle-btn {
    padding: 0 15px;
    margin-left: -20px;
    font-size: 36px;
    color: white;
    cursor: pointer;
    line-height: 60px;
    &:hover {
      background-color: #4292CF;
    }
  }
  .system-title {
    font-size: 28px;
    color: white;
  }
  @media screen and (max-width:640px){
    .system-title{
      font-size:18px;
    }
  }
  .welcome{
    color: white;
  }
}
</style>



