<template>
  <div class="group">
     <el-button type="primary" :disabled="isDisabled" @click="handleAdd">添加分組</el-button>
     <el-table
        :data="groupData"
        border
        style="width: 100%;margin-top:10px;">
        <el-table-column
          label="组名"
          prop="name"
        >
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
              <el-button type="danger" @click="handleDelete(scope.row.group_id)">删除分组</el-button>
          </template>
        </el-table-column>
      </el-table>
  </div>
</template>

<script>
import {getGroup, delGroup, addGroup} from '@/api/index'
export default {
  name: 'Group',
  data () {
    return {
      groupData: [],
      isDisabled: false
    }
  },
  methods: {
    // 获取分组数据
    getGroupdata () {
      getGroup()
        .then(res => {
          if (res.code === 1) {
            this.groupData = res.groups
          }
        })
    },
    // 删除分组
    handleDelete (id) {
      this.$confirm('此操作将永久删除该分组以及分组下的所有链接, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {group_id: id.toString()}
        delGroup(JSON.stringify(params))
          .then(res => {
            if (res.code === '1') {
              this.$message({
                type: 'success',
                message: '删除成功!'
              })
              this.getGroupdata()
            }
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 添加分组
    handleAdd (btn) {
      this.isDisabled = true
      let params = {add_group: '1'}
      addGroup(JSON.stringify(params))
        .then(res => {
          if (res.code === 1) {
            this.$message.success('添加分组成功')
            this.getGroupdata()
            this.isDisabled = false
          } else {
            this.$message.error('添加分组失败')
            this.isDisabled = false
          }
        })
    }
  },
  mounted () {
    this.getGroupdata()
  }
}
</script>

<style>
@media screen and (max-width: 640px){
.el-message-box{
    width: 300px !important;
  }
}
@media screen and (max-width: 640px){
.el-dialog__footer{
    padding: 10px 0px 20px;
    text-align: center;
  }
}
</style>
<template>
  <div class="group">
     <el-button type="primary" :disabled="isDisabled" @click="handleAdd">添加分組</el-button>
     <el-table
        :data="groupData"
        border
        style="width: 100%;margin-top:10px;">
        <el-table-column
          label="组名"
          prop="name"
        >
        </el-table-column>
        <el-table-column
          label="操作">
          <template slot-scope="scope">
              <el-button type="danger" @click="handleDelete(scope.row.group_id)">删除分组</el-button>
          </template>
        </el-table-column>
      </el-table>
  </div>
</template>

