    }
  },
  mounted () {
    this.getGroupdata()
  }
}
</script>

<style>
@media screen and (max-width: 640px){
.el-message-box{
    width: 300px !important;
  }
}
@media screen and (max-width: 640px){
.el-dialog__footer{
    padding: 10px 0px 20px;
    text-align: center;
  }
}
</style>



